	module.exports = {"data":{"wordpressPost":{"title":"Review for BMJ Open","content":"<p>Keep your analytical skills sharp, receive public acknowledgement of your efforts and save money on publication charges!</p>\n<p><em>BMJ Open</em> is now reviewing articles for publication. Peer reviewing is a good way to hone your analytical skills and to get a &#8216;first look&#8217; at forthcoming research. Our system of open peer review – publishing reviewers&#8217; comments – provides public acknowledgement of your input. It is also helpful if you need to demonstrate your reviewing activity for continuing professional development purposes.</p>\n<p>You can register as a reviewer at our <a href=\"http://mc.manuscriptcentral.com/bmjopen\" target=\"_blank\">submission site</a>; once you have registered, drop us an <a href=\"mailto:editorial.bmjopen@bmjgroup.com\" target=\"_blank\">email</a> and let us know.</p>\n<p>In recognition of your support, as a <em>BMJ Open</em> reviewer you will receive a 25% discount on article processing charges if you are the corresponding author of a paper submitted to the journal within a year of returning your review.</p>\n<p>Our instructions for reviewers are <a href=\"http://blogs.bmj.com/bmjopen/instructions-for-reviewers/\" target=\"_blank\">here</a>.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"BMJ PWA demo"}}},"pathContext":{"id":"c80c112a-50b5-52bc-9ba6-380b4c3a32eb"}}

